body {
  margin: 0;
  padding: 0;
  background: rgb(248, 247, 243);
}

nav ul {
  list-style: none;
  background-color: rgb(68, 68, 68);
  text-align: left;
  padding: 0 0.5rem;
  margin: 0 0 0.5rem 0;
  border-bottom: 5px solid rgb(214, 139, 25);
}

nav li {
  font-family: 'Oswald', sans-serif;
  font-size: 0.8rem;
  line-height: 32px;
  height: 32px;
  vertical-align: center;
}

nav a {
  padding-left: 4px;
  text-decoration: none;
  color: #fff;
  display: block;
  transition: 0.3s background-color;
}

nav a:hover {
  background-color: rgb(214, 139, 25);
}

nav a.active {
  background-color: rgb(214, 139, 25);
  color: #444;
  cursor: default;
}

@media screen and (min-width: 600px) {
  nav li {
    width: 80px;
    /* border-bottom: none; */
    height: 20px;
    line-height: 20px;
    text-align: center;
  }

  nav a {
    padding-left: 0;
  }

  /* Option 1 - Display Inline */
  nav li {
    display: inline-block;
    margin-right: -4px;
  }

  /* Options 2 - Float 
    nav li {
      float: left;
    }
    nav ul {
      overflow: auto;
      width: 600px;
      margin: 0 auto;
    }
    nav {
      background-color: #444;
    }
    /* */
}
